const initialState = {
	heroes: [],
	heroesLoadingStatus: 'idle',
	filters: {},
	heroFilterStatus: 'all',
};

const reducer = (state = initialState, action) => {
	switch (action.type) {
		case 'HEROES_FETCHING':
			return {
				...state,
				heroesLoadingStatus: 'loading',
			};
		case 'HEROES_FETCHED':
			return {
				...state,
				heroes: action.payload,
				heroesLoadingStatus: 'idle',
			};
		case 'HEROES_FETCHING_ERROR':
			return {
				...state,
				heroesLoadingStatus: 'error',
			};
		case 'HERO_DELETE':
			return {
				...state,
				heroes: state.heroes.filter(
					hero => hero.id !== +action.payload.id
				),
			};
		case 'ADD_HERO':
			return {
				...state,
				heroes: [
					...state.heroes,
					{
						id: action.payload.heroId,
						name: action.payload.heroName,
						description: action.payload.heroDescription,
						element: action.payload.heroElement,
					},
				],
			};
		case 'FILTERS_FETCHED':
			return {
				...state,
				filters: action.payload.filtersArr,
			};
		case 'SET_FILTERS':
			return {
				...state,
				heroFilterStatus: action.payload.filterStatus,
			};
		default:
			return state;
	}
};

export default reducer;
